import Vue from 'vue'
import App from './App.vue'

// Vuesax Component Framework
import Vuesax from 'vuesax'
import 'material-icons/iconfont/material-icons.css' //Material Icons
import 'vuesax/dist/vuesax.css'; // Vuesax
Vue.use(Vuesax)


// Theme Configurations
import '../themeConfig.js'


// Globally Registered Components
import './globalComponents.js'


// Styles: SCSS
import './assets/scss/main.scss'


// Tailwind
import '@/assets/css/main.css';


// Vue Router
import router from './router'


// Vuex Store
import store from './store/store'


// Vuesax Admin Filters
import './filters/filters'

// Vuejs - Vue wrapper for hammerjs
import { VueHammer } from 'vue2-hammer'
Vue.use(VueHammer)


// PrismJS
import 'prismjs'
import 'prismjs/themes/prism-tomorrow.css'


// Feather font icon
require('./assets/css/iconfont.css')


Vue.config.productionTip = false;

// const host = "http://127.0.0.1:3000/";
const host = "/";

import axios from 'axios';

const http = axios.create({
    baseURL: host + 'api/v1/',
});

axios.get('/status').then(res => {
    store.commit('os/setCpuRam', res.data)
}).catch(e => console.log(e));



Vue.prototype.$http = http;

import io from 'socket.io-client';

let socket = io(host);

socket.on('connect', () => {
    store.commit('os/setOnline', true);
});

socket.on('disconnect', () => {
    store.commit('os/setOnline', false);
});

socket.on('info:os', (data) => {
    store.commit('os/setCpuRam', data);
});

socket.on('log:http', (data) => {
    store.commit('http/addItem', data);
});

Vue.prototype.$io = socket;

import moment from 'moment';
moment.locale('ru');
Vue.prototype.$moment = moment;

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
