export default {
    namespaced: true,
    state: {
        ip: null,
        cpu: 0,
        ram: 0,
        isOnline: false,
        startTime: new Date(),
        temp: 0,
        volt: 0,
        uptime: 0,
        version: '',
        sections: 0,
        modules: 0
    },
    mutations: {
        setCpuRam(state, data){
            state.cpu = data.cpu;
            state.ram = data.ram;
            state.ip = data.ip;
            state.startTime = data.start_time;
            state.temp = data.temp;
            state.volt = data.volt;
            state.version = data.version;
            state.uptime = data.uptime;
            state.sections = data.sections;
            state.modules = data.modules;
        },
        setOnline(state, data){
            state.isOnline = data;
            if(!data){
                state.cpu = 0;
                state.ram = 0;
            }
        }
    }
}
